.footer {
  position: relative;
  z-index: 5;
  background: #161515;
  padding: 60px 0;
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 645px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 30px;
    margin: 0 auto;
  }
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
  }
  &__item {
    width: 80px;
    img {
      width: 100%;
    }
  }
  &__title {
    color: #fff;
  }
  @media screen and (min-width: 1201px) {
    &__title {
      font-size: 40px;
    }
  }
  &__btn {
    position: relative;
    height: 65px;
    display: inline-flex;
    border-radius: 84px;
    padding: 5px 45px;
    span {
      font-size: 18px;
    }
  }
  &__img {
    position: absolute;
    top: 47px;
    right: 170px;
    width: 340px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: 100px;
    bottom: 0;
    width: 340px;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__img {
      top: 30px;
      right: 50px;
      width: 200px;
    }
    &__img2 {
      left: 50px;
      width: 200px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__item {
      width: 50px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__btn {
      height: 55px;
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
  }
}