.main {
  position: relative;
  padding: 30px 0 160px;
  &:before {
    content: '';
    position: absolute;
    top: -40px;
    left: 40px;
    width: 204px;
    height: 204px;
    background: #FFDFD0;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    right: 24px;
    bottom: 100px;
    width: 204px;
    height: 204px;
    background: #FFDFD0;
    filter: blur(100px);
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    gap: 8px;
  }
  &__desc {
    max-width: 700px;
  }
  &__group {
    position: relative;
    width: calc(100% - 700px);
  }
  &__img {
    position: absolute;
    top: 0;
    right: -78px;
    max-width: 570px;
    &:after {
      content: '';
      position: absolute;
      top: -24px;
      right: -1px;
      width: 500px;
      height: 500px;
      background: #E6E6E6;
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
  &__title {
    font-size: 61px;
    line-height: 127%;
    font-weight: 900;
    color: #161515;
    margin-top: 30px;
  }
  &__text {
    margin-top: 16px;
  }
  &__btn {
    position: relative;
    height: 65px;
    display: inline-flex;
    border-radius: 84px;
    padding: 5px 45px;
    margin-top: 30px;
    span {
      font-size: 18px;
    }
  }
  &__phone {
    position: absolute;
    z-index: 3;
    left: -5px;
    bottom: 120px;
  }
  &__calendar {
    position: absolute;
    z-index: 3;
    top: 160px;
    right: -40px;
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: -90px;
    width: 343px;
    img {
      width: 100%;
    }
  }
}
.booking {
  position: relative;
  padding: 160px 0 145px;
  &:after {
    content: '';
    position: absolute;
    top: 280px;
    right: 180px;
    width: 350px;
    height: 350px;
    background: rgba(255, 223, 208, 0.7);
    filter: blur(150px);
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 880px;
    &:after {
      content: '';
      position: absolute;
      z-index: -1;
      top: -40px;
      left: -170px;
      width: 627px;
      height: 627px;
      background: #E6E6E6;
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__title {
    max-width: 850px;
    margin-top: 30px;
  }
  &__text {
    margin-top: 20px;
  }
  &__img {
    position: relative;
    margin-top: 30px;
    img {
      width: 100%;
      box-shadow: 0px 0px 193.6px 0px rgba(229, 229, 229, 0.25);
    }
  }
  &__calendar {
    position: absolute;
    top: 160px;
    right: -40px;
  }
  &__img2 {
    position: absolute;
    right: 180px;
    bottom: 0;
    width: 315px;
    img {
      width: 100%;
    }
  }
}
.events {
  position: relative;
  padding: 145px 0 160px;
  &:after {
    content: '';
    position: absolute;
    left: 150px;
    bottom: -50px;
    width: 350px;
    height: 350px;
    background: rgba(255, 223, 208, 0.7);
    filter: blur(150px);
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    &:after {
      content: '';
      position: absolute;
      z-index: -1;
      top: -70px;
      right: -100px;
      width: 1090px;
      height: 1090px;
      background: #E6E6E6;
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
    &:nth-child(odd) {
      text-align: right;
    }
  }
  &__img {
    width: 360px;
    flex-shrink: 0;
    img {
      width: 100%;
      box-shadow: 0px 0px 76.5px 0px rgba(194, 194, 194, 0.25);
    }
  }
  &__desc {
    width: calc(100% - 400px);
  }
  &__title {
    margin-top: 16px;
  }
  &__text {
    margin-top: 32px;
  }
  &__btn {
    position: relative;
    height: 65px;
    display: inline-flex;
    border-radius: 84px;
    padding: 5px 45px;
    margin-top: 30px;
    span {
      font-size: 18px;
    }
  }
  &__phone {
    position: absolute;
    top: 240px;
    right: -75px;
  }
}

@media screen and (max-width: 1600px) {
  .main {
    &__img2 {
      display: none;
    }
  }
  .booking {
    &__img2 {
      right: 30px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    padding-bottom: 45px;
    &:after {
      bottom: 200px;
    }
    &__img {
      position: static;
    }
    &__img2 {
      bottom: 100px;
      width: 300px;
    }
    &__calendar {
      right: -15px;
    }
  }
  .booking {
    padding: 45px 0;
    &__inner {
      &:after {
        left: -50px;
        width: 500px;
        height: 500px;
      }
    }
    &__img2 {
      bottom: -50px;
      width: 250px;
    }
  }
  .events {
    padding: 45px 0 90px;
    &__inner {
      &:after {
        right: -40px;
        width: 800px;
        height: 800px;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__img {
      &:after {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 400px;
        height: 400px;
      }
    }
  }
  .booking {
    &__img2 {
      display: none;
    }
  }
  .events {
    &__phone {
      top: -20px;
      right: -5px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__desc {
      width: 60%;
      max-width: 100%;
    }
    &__group {
      width: 40%;
    }
    &__title {
      font-size: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__group {
      width: 100%;
      max-width: 500px;
      margin: 0 auto;
    }
    &__title {
      font-size: 36px;
      margin-top: 16px;
    }
  }
  .booking {
    &__title {
      margin-top: 16px;
    }
    &__text {
      margin-top: 16px;
    }
    &__img {
      margin-top: 16px;
    }
    &__calendar {
      top: 140px;
      right: -5px;
    }
  }
  .events {
    &__text {
      margin-top: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__group {
      max-width: 400px;
    }
    &__img {
      &:after {
        width: 350px;
        height: 350px;
      }
    }
    &__img2 {
      width: 200px;
    }
  }
  .events {
    &__inner {
      padding-bottom: 105px;
      &:after {
        width: 600px;
        height: 600px;
      }
    }
    &__group {
      flex-direction: column;
      text-align: start !important;
      gap: 20px;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 300px;
      order: 1;
      margin: 0 auto;
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: max-content;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 30px;
    &__title {
      font-size: 32px;
    }
    &__btn {
      height: 55px;
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
    &__calendar {
      top: -10px;
      right: 0;
    }
  }
  .booking {
    padding: 30px 0;
    &__inner {
      &:after {
        width: 400px;
        height: 400px;
      }
    }
    &__calendar {
      display: none;
    }
  }
  .events {
    padding: 30px 0 60px;
    &__inner {
      gap: 20px;
      padding-bottom: 85px;
      &:after {
        top: -40px;
        right: -50px;
        width: 400px;
        height: 400px;
      }
    }
    &__btn {
      height: 55px;
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
    &__phone {
      display: none;
    }
  }
}

@media screen and (max-width: 450px) {
  .booking {
    &__inner {
      &:after {
        left: -30px;
        width: 300px;
        height: 300px;
      }
    }
  }
  .events {
    &__inner {
      &:after {
        right: -30px;
        width: 300px;
        height: 300px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__img {
      &:after {
        width: 300px;
        height: 300px;
      }
    }
  }
}