.btn-pink {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #FF7980;
  &:hover {
    background: #ffaaaf;
  }
  span {
    line-height: 120%;
    color: #fff;
  }
  .absolute-text {
    position: absolute;
    top: -8px;
    left: 50%;
    transform: translate(-50%, 0);
    background: #332E2E;
    border-radius: 43px;
    padding: 4px 8px;
    font-size: 14px;
    line-height: 105%;
    color: #fff;
  }
  i {
    max-width: 14px;
    svg {
      max-width: 100%;
      width: auto;
      height: auto;
    }
  }
}
.wrapper {
  overflow: hidden;
}
.h2 {
  font-size: 48px;
  line-height: 160%;
  font-weight: 900;
  color: #161515;
}
.subtitle {
  font-size: 30px;
  line-height: 105%;
  font-weight: 700;
  color: #A0A0A0;
  text-transform: uppercase;
}
.text20 {
  font-size: 20px;
  line-height: 170%;
  color: #332E2E;
}
.calendar {
  width: 64px;
  height: 64px;
  background: #161515;
  border-radius: 21px;
  &:hover {
    background: #3e3e3e;
  }
  svg {
    max-width: 90%;
    width: auto;
    height: auto;
  }
}
.phone {
  width: 53px;
  height: 53px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #FF7980;
  border-radius: 18px;
  &:hover {
    background: #ffaaaf;
  }
  svg {
    max-width: 90%;
    width: auto;
    height: auto;
  }
}

@media screen and (max-width: 1200px) {
  .h2 {
    font-size: 40px;
  }
  .subtitle {
    font-size: 26px;
  }
  .text20 {
    font-size: 16px;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 30px;
  }
  .subtitle {
    font-size: 20px;
  }
}

@media screen and (max-width: 576px) {
  .btn-pink {
    .absolute-text {
      font-size: 12px;
    }
  }
  .h2 {
    font-size: 24px;
  }
  .subtitle {
    font-size: 18px;
  }
  .calendar {
    width: 53px;
    height: 53px;
    border-radius: 18px;
  }
}