.header {
  position: relative;
  z-index: 5;
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__logo {
    width: 146px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__btn {
    height: 50px;
    border-radius: 64px;
    padding: 5px 30px;
    span {
      font-size: 14.5px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 16px 0;
    &__logo {
      width: 100px;
    }
    &__btn {
      height: 40px;
      padding: 5px 20px;
    }
  }
}